@charset "utf-8";
@import "../includes/color";

.scr-navi.brand-index {
  a {
    font-size: 4.14vw;
  }
  .swiper-wrapper {
    .swiper-slide {
      &:first-child {
        padding: 0 2.66vw 0 4vw;
      }
      &:last-child {
        padding-right: 6vw;
      }
    }
  }
}
.scr-navi.brand-list {
  a {
    font-size: 4.14vw;
  }
}
.brand-top {
  .swiper-slide {
    width: 100%;
    a {
      display: block;
      img {
        width: 100%;
      }
    }
  }
}
.brand-shop {
  .swiper-slide {
    width: 33.3333%;
    a {
      display: block;
      position: relative;
      .img {
        display: block;
        img {
          width: 100%;
        }
      }
      .txt {
        direction: block;
        position: absolute;
        top: 50%;
        margin-top: -1.333vw;
        width: 100%;
        color: #fff;
        font-size: 2.666vw;
        font-weight: 700;
        line-height: 2.933vw;
        text-align: center;
      }
    }
  }
}
.brand-main {
  position: relative;
  height: 133.333vw;
  .img-area {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: $midnight-blud-color;
  }
  .txt-area {
    position: absolute;
    top: 80vw;
    left: 7.466vw;
    font-size: 8.266vw;
    font-family: 'helvetica';
    color: #fff;
    font-weight: 700;
    &::after {
      content: '';
      display: block;
      margin-top: 5.2vw;
      width: 28.533vw;
      height: .266vw;
      background: #fff;
    }
  }
  .scr-navi {
    bottom: 2vw;
    position: absolute;
    .swiper-slide {
      background: none;
      padding: 0 3.733vw;
      &:first-child {
        padding-left: 7.466vw;
      }
      &:last-child {
        padding-right: 7.466vw;
      }
      a {
        color: #fff;
        font-weight: 300;
        font-size: 4vw;
      }
    }
  }
}
.prod-list-top.mt2 {
  margin: 12vw 0 9.333vw;
}
.prod-list-top.mt3 {
  margin: 10.666vw 0 6vw;
}
.bnr-more {
  display: block;
  width: 7.733vw;
  height: 7.733vw;
  background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
  background-size: 100%;
  font-size: 0;
  text-indent: -9999px;
  margin: 10.666vw auto 16.4vw;
}
.brand-bts {
  overflow: hidden;
  padding: 0 4.8vw 12vw;
  a {
    display: block;
    border: 2px #000 solid;
    height: 9.066vw;
    line-height: 8vw;
    text-align: center;
    font-size: 3.733vw;
    color: #1f1f1f;
    font-weight: 700;
    width: 43.733vw;
    box-sizing: border-box;
    float: left;
  }
  a + a {
    margin-left: 2.933vw;
  }
}
.brand-intro {
  .b-top {
    padding: 9.333vw 7.333vw 6.133vw;
    h3 {
      font-weight: 700;
      font-family: 'helvetica';
      font-size: 4.8vw;
      color: #1f1f1f;
    }
  }
  .b-img {
    width: 85.333vw;
  }
  .b-disc {
    padding: 8.933vw 6.666vw 15.333vw;
    p {
      font-size: 3.733vw;
      margin-top: 8vw;
      letter-spacing: -.213vw;
      line-height: 6.133vw;
      color: #1f1f1f;
      &:first-child {
        margin-top: 0;
      }
    }
  }
  h4 {
    font-size: 4.266vw;
    font-weight: 700;
    color: #1f1f1f;
    font-family: 'helvetica';
    padding: 0 7.333vw 5.866vw;
  }
  .b-collection {
    background: #ecdfce;
    padding: 4.933vw 0 0;
    a {
      display: block;
      padding: 2.8vw 0 4.8vw;
    }
    img {
      width: 54vw;
      margin: 0 auto;
    }
    span {
      font-size: 2.933vw;
      color: #a7a7a7;
      text-decoration: underline;
      text-align: center;
      display: block;
      margin-top: 4.8vw;
    }
  }
}
.h1-top {
  position: relative;
  .pop-close {
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    background-size: 100%;
    top: 5.333vw;
    right: 4.8vw;
    width: 4vw;
    height: 4vw;
    font-size: 0;
    text-indent: -9999px;
    display: block;
    position: absolute;
  }
  .pop-close.close2 {
    top: 6.4vw;
  }
  h1 {
    font-size: 4.533vw;
    color: #1f1f1f;
    text-align: center;
    font-weight: 700;
    padding: 5.333vw 0 0;
  }
}


// Brand.mainNew css
.brand-main-img {
  position: relative;

  .brand-main-img-holder {
    padding-bottom: 54.4%;
  }

  img {
    position: absolute;
    top: 0;
    width: 100%;
  }
}

.brand-main-new {
  position: relative;
  height: 54.4vw;
  padding-top: 14.4vw;
  text-align: center;
  .wrap-txt {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    h2 {
      font-size: 5.866vw;
      font-weight: 700;
      color: #242424;
    }
    p {
      margin-top: 4vw;
      font-size: 2.933vw;
      color: #aaa;
    }
  }
}
.prod-big-bnr {
  .h-space {
    margin-bottom: 2.133vw;
  }
}
.shop-list {
  position: relative;
  .shop {
    position: relative;
    font-size: 0;
    img {
      width: 33.3vw;
      &:last-child {
        margin-left: -33.3vw;
      }
    }
    p {
      position: absolute;
      top: 50%;
      width: 100%;
      text-align: center;
      font-size: 2.666vw;
      color: #fff;
    }
  }
  .swiper-pagination {
    position: absolute;
    top: -22%;
    right: 5%;
    font-size: 3.2vw;
    color: #ccc;
    .swiper-pagination-current {
      color: #1f1f1f;
    }
  }
}
.tab {
  padding-top: 14.4vw;
  li {
    float: left;
    width: 25%;
    padding: 7.33vw 0 4.266vw;
    text-align: center;
    a {
      font-size: 3.466vw;
      font-weight: 700;
      color: #1f1f1f;
    }
  }
  .on {
    border-bottom: 2px solid #000;
  }
}
.brand-skin {
  .skin-wide {
    img {
      width: 100%;
    }
  }
  .bottom {
    overflow: hidden;
    padding-top: 2.933vw;
    img {
      float: left;
      width: 50%;
    }
  }
}
.season-only {
  padding-bottom: 13.066vw;
  border-bottom: 1.066vw solid #000;
  .season-only-list {
    overflow: hidden;
    li {
      float: left;
      width: 33.3%;
      cursor: pointer;
      a {
        display: block;
      }
      p {
        font-size: 2.933vw;
        font-weight: bold;
        color: #1f1f1f;
        text-align: center;
        text-transform: uppercase;
      }
    }
  }
}
.btm-bnr {
  padding-bottom: 12.8vw;
  div {
    width: 100%;
    margin-bottom: 2.133vw;
    a {
      display: block;
      width: 100%;
    }
  }
}


@media (min-width: 750px) {
  .list-scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide {
        padding: 0 20px;
        a {
          font-size: 31px;
        }
        &:first-child {
          padding-left: 0;
        }
      }
    }
    .swiper-wrapper + .icn-navi {
      width: 35px;
      height: 115px;
      right: -36px;
    }
    .swiper-wrapper + .icn-navi.left {
      left: -36px;
    }
  }
  .scr-navi.brand-index {
    a {
      font-size: 31px;
    }
    .swiper-wrapper {
      .swiper-slide {
        &:last-child {
          padding-right: 45px;
        }
      }
    }
  }
  .brand-main {
    height: 1000px;
    .txt-area {
      top: 600px;
      left: 56px;
      font-size: 62px;
      &::after {
        margin-top: 39px;
        width: 214px;
        height: 2px;
      }
    }
    .scr-navi {
      bottom: 15px;
      .swiper-slide {
        padding: 0 28px;
        &:first-child {
          padding-left: 56px;
        }
        &:last-child {
          padding-right: 56px;
        }
        a {
          font-size: 31px;
        }
      }
    }
  }
  .brand-shop {
    .swiper-slide {
      a {
        .txt {
          margin-top: -11px;
          font-size: 20px;
          line-height: 22px;
        }
      }
    }
  }
  .prod-list-top.mt2 {
    margin: 90px 0 70px;
  }
  .prod-list-top.mt3 {
    margin: 80px 0 45px;
  }
  .bnr-more {
    width: 58px;
    height: 58px;
    margin: 80px auto 123px;
  }
  .brand-bts {
    padding: 0 36px 90px;
    a {
      display: block;
      border: 4px #000 solid;
      height: 68px;
      line-height: 60px;
      font-size: 30px;
      width: 328px;
    }
    a + a {
      margin-left: 22px;
    }
  }
  .brand-intro {
    .b-top {
      padding: 70px 55px 46px;
      h3 {
        font-size: 36px;
      }
    }
    .b-img {
      width: 640px;
    }
    .b-disc {
      padding: 67px 50px 115px;
      p {
        font-size: 28px;
        margin-top: 60px;
        letter-spacing: -1.6px;
        line-height: 46px;
      }
    }
    h4 {
      font-size: 32px;
      padding: 0 55px 44px;
    }
    .b-collection {
      padding: 73px 0 36px;
      img {
        width: 405px;
      }
      span {
        font-size: 22px;
        margin-top: 36px;
      }
    }
  }
  .h1-top {
    .pop-close {
      top: 40px;
      right: 36px;
      width: 30px;
      height: 30px;
    }
    .pop-close.close2 {
      top: 48px;
    }
    h1 {
      font-size: 34px;
      padding: 40px 0 0;
    }
  }
  // Brand.mainNew css
  .brand-main-new {
    height: 408px;
    padding-top: 108px;
    .wrap-txt {
      h2 {
        font-size: 44px;
      }
      p {
        margin-top: 30px;
        font-size: 22px;
      }
    }
  }
  .prod-big-bnr {
    .h-space {
      margin-bottom: 16px;
    }
  }
  .shop-list {
    .shop {
      img {
        width: 250px;
      }
      p {
        font-size: 20px;
      }
    }
    .swiper-pagination {
      font-size: 24px;
    }
  }
  .tab {
    padding-top: 108px;
    li {
      padding: 55px 0 32px;
      a {
        font-size: 26px;
      }
    }
  }
  .brand-skin {
    .bottom {
      padding-top: 22px;
    }
  }
  .season-only {
    padding-bottom: 98px;
    border-bottom: 8px solid #000;
    .season-only-list {
      li {
        p {
          font-size: 22px;
        }
      }
    }
  }
  .btm-bnr {
    padding-bottom: 96px;
    div {
      margin-bottom: 16px;
    }
  }
}
